@font-face {
    font-family: "flower";
    src: url(/static/media/Fowler-lW9e.9214cefa.ttf);
}

.title {
    padding-top: 10%;
    color: #fff;
}

.titleProduct {
    padding-top: 5%;
    font-family: "flower";
    font-size: 50pt;
}

.cardHome {
    min-height: 15rem;
    border-radius: 2rem;
    overflow: hidden;
    position: relative;
}

.cardText {
    position: absolute;
    z-index: 5;
    left: 50%;
    bottom: 0%;
    -webkit-transform: translate(-50%, 0%);
            transform: translate(-50%, 0%);
    color: white;
    width: 100%;
    text-align: center;
    background-color: black;
    font-weight: 650;
    font-size: 1rem;
    height: 10%;
    display: flex;
    justify-content: center;
    align-items: center;
}

#img-home {
    border-radius: 2rem;
    position: absolute;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.background_gradient {
    z-index: 3;
    position: absolute;
    width: 100%;
    height: 100%;
    background: linear-gradient(rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.3));
}
#sidebar {
    float: left;
    /* z-index: 5; */
    /* position: fixed; */
    /* left: 0px; */
    width: 250px;
    /* margin-left: -250px; */
    overflow-y: auto;
    height: 700vh;
    background: #37474F;
    transition: all 0.5s ease;
}

#sidebar header {
    background-color: #263238;
    color: #ffff;
    font-size: 20px;
    line-height: 52px;
    text-align: center;
}

#sidebar .nav a {
    background: none;
    border-bottom: 1px solid #455A64;
    color: #CFD8DC;
    font-size: 14px;
    padding: 16px 24px;
}

#sidebar .nav a:hover {
    background: none;
    color: #ECEFF1;
}

#sidebar .nav a i {
    margin-right: 16px;
}
.cardProduct {
    background-color: white;
    color: #272727;
    padding: 20px;
    border-radius: 10px;
    border: 0px;
    border-color: tomato;
    border-style: solid;
    transition: 200ms;
    max-width: 380px;
}

.card:hover {
    transition: 200ms;
    border: 3px;
    border-color: tomato;
    border-style: solid;
}

.card-header {
    display: flex;
    flex-wrap: nowrap;
    align-items: center;

}

.profile {
    width: 2.5rem;
    height: 2.5rem;
    background-color: tomato;
    border-radius: 50%;
    margin: 10px;
    font-size: 20pt;
    text-align: center;
    font-weight: bold;
    color: white;
    justify-content: center;
}

.letter {
    vertical-align: center;
    text-transform: uppercase;
}

.card-title-group {
    justify-content: center;
}

.card-title {
    flex: 0.5 1;
    font-size: large;
    margin-bottom: 0;
    margin-top: 0;
}

.card-date {
    flex: 0.3 1;
    font-size: small;
    margin-top: 0;
}

.card-image {
    border-radius: 10px;
    margin-bottom: 10px;
    margin-top: 10px;
}

.card-like-icon {
    height: 25px;
}

.card-text {
    width: 350px;
    font-size: medium;
}

.card-like-bar {
    display: flex;
    align-items: center;
    margin-top: 20px;
    justify-content: space-between;
}

.like-text {
    font-size: small;
    margin-left: 10px;
}
.body-class-login {
  background: #f6f5f7;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  font-family: "Montserrat", sans-serif;
  height: 95vh;
  margin: -15px 0 45px;
}

.Span {
  font-size: 12px;
}

.Anchor {
  color: #333;
  font-size: 14px;
  text-decoration: none;
  margin: 15px 0;
}

.btn {
  border-radius: 20px;
  border: 1px solid #ffffff;
  background-color: #00c060;
  color: #ffffff;
  font-size: 12px;
  font-weight: bold;
  padding: 12px 45px;
  margin-bottom: 7%;
  letter-spacing: 1px;
  text-transform: uppercase;
  transition: -webkit-transform 80ms ease-in;
  transition: transform 80ms ease-in;
  transition: transform 80ms ease-in, -webkit-transform 80ms ease-in;
}

.btn1 {
  background-color: transparent;
  border-radius: 20px;
  border: 1px solid #ffffff;
  background-color: #00c060;
  margin-top: 20px;
  color: #ffffff;
  
  font-size: 20px;
  font-weight: bold;

  letter-spacing: 1px;
  text-transform: uppercase;
  transition: -webkit-transform 80ms ease-in;
  transition: transform 80ms ease-in;
  transition: transform 80ms ease-in, -webkit-transform 80ms ease-in;
}

.btn2 {
  background-color: transparent;
  border-radius: 20px;
  border: 1px solid #ffffff;
  background-color: #00c060;
  margin-top: 10px;
  color: #ffffff;
  font-size: 20px;
  font-weight: bold;
  letter-spacing: 1px;
  text-transform: uppercase;
  transition: -webkit-transform 80ms ease-in;
  transition: transform 80ms ease-in;
  transition: transform 80ms ease-in, -webkit-transform 80ms ease-in;
}

.Span {
  margin-top: 10px;
  margin-bottom: 5px;
}

.Form {
  background-color: #ffffff;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 0 50px;
  height: 100%;
  text-align: center;
}

input {
  background-color: #eee;
  border: none;
  padding: 12px 15px;
  margin: 8px 0;
  width: 100%;
}

.containerLogin {
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
  position: relative;
  overflow: hidden;
  width: 768px;
  max-width: 100%;
  min-height: 480px;
}

.form-container {
  position: absolute;
  top: 0;
  height: 100%;
  transition: all 0.6s ease-in-out;
}

.sign-in-container {
  left: 0;
  width: 50%;
  z-index: 2;
}

.containerLogin.right-panel-active .sign-in-container {
  -webkit-transform: translateX(100%);
          transform: translateX(100%);
}

.sign-up-container {
  left: 0;
  width: 50%;
  opacity: 0;
  z-index: 1;
}

.containerLogin.right-panel-active .sign-up-container {
  -webkit-transform: translateX(100%);
          transform: translateX(100%);
  opacity: 1;
  z-index: 5;
  -webkit-animation: show 0.6s;
          animation: show 0.6s;
}

.overlay-container {
  position: absolute;
  top: 0;
  left: 50%;
  width: 50%;
  height: 100%;
  overflow: hidden;
  transition: -webkit-transform 0.6s ease-in-out;
  transition: transform 0.6s ease-in-out;
  transition: transform 0.6s ease-in-out, -webkit-transform 0.6s ease-in-out;
  z-index: 100;
}

.containerLogin.right-panel-active .overlay-container {
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
}

.overlay {
  background: #00c060;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 0 0;
  color: #ffffff;
  position: relative;
  left: -100%;
  height: 100%;
  width: 200%;
  -webkit-transform: translateX(0);
          transform: translateX(0);
  transition: -webkit-transform 0.6s ease-in-out;
  transition: transform 0.6s ease-in-out;
  transition: transform 0.6s ease-in-out, -webkit-transform 0.6s ease-in-out;
}

.containerLogin.right-panel-active .overlay {
  -webkit-transform: translateX(50%);
          transform: translateX(50%);
}

.overlay-panel {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 0 40px;
  text-align: center;
  top: 0;
  height: 100%;
  width: 50%;
  -webkit-transform: translateX(0);
          transform: translateX(0);
  transition: -webkit-transform 0.6s ease-in-out;
  transition: transform 0.6s ease-in-out;
  transition: transform 0.6s ease-in-out, -webkit-transform 0.6s ease-in-out;
}

.overlay-left {
  -webkit-transform: translateX(-20%);
          transform: translateX(-20%);
}

.containerLogin.right-panel-active .overlay-left {
  -webkit-transform: translateX(0);
          transform: translateX(0);
}

.overlay-right {
  right: 0;
  -webkit-transform: translateX(0);
          transform: translateX(0);
}

.containerLogin.right-panel-active .overlay-right {
  -webkit-transform: translateX(20%);
          transform: translateX(20%);
}

.social-container {
  margin: 5px 0px;
